.content {
  padding: 0 23px 30px 23px;

  display: flex;
  flex-wrap: wrap;
}
.block {
  width: 50%;
}
.title {
  width: fit-content;
  padding: 0 20px;
  font-size: 20px;
  font-weight: bold;
  border-top: 1px solid #000000;
  border-left: 1px solid #000000;
  border-right: 1px solid #000000;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.card {
  height: 100%;
  padding: 5px;
  border-top: 1px solid #000000;
}
.right {
  border-left: 1px solid #000000;
}
